import React, { Component } from 'react';
import Select from 'react-select';

class AreaOfPracticeSelection extends Component {
    constructor(props){
        super(props);
    }
    render () {
        return (
            <>
                <div>
                    <label id="areas-of-practice-select-label" htmlFor="areas-of-practice-select">I would like help with </label>
                    <Select maxMenuHeight={500} className="area_selection" isSearchable={false} styles={this.props.styles}name="area of practice home selection" placeholder={this.props.text} options={this.props.options} onChange={this.props.onChange} />
                </div>
            </>
        )
    }
}

export default AreaOfPracticeSelection;
